.stage {
  bottom: 0 !important;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden ;
	background: linear-gradient(-45deg, darkslateblue, skyblue, darkslateblue, darkslateblue);
  background-size: 400% 400%;
	animation: gradient 15s ease infinite;
  }
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.scale {
  display: block;
  width: 50%;
  margin: 20px auto;
  animation: scale 0.5s infinite alternate;
}
 
@keyframes scale { 

    0% { 
        transform: translateX(-50px);
    } 
    
    100% { 
        transform: translateX(50px);
    } 
}

  
